.select {
  position: relative;
}

.select.active .select__body {
  height: auto;
  max-height: 500px;
}

.select.active .select__icon {
  transform: scaleY(-1);
}
.select.active .select__body {
  display: block;
}
.select.active .select__header {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}

.select__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 130px;
  height: 26px;
  padding: 0 8px;
  border: 2px solid #474747;
  cursor: pointer;
  grid-gap: 8px;
}

.select__current {
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.select__icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 13px;
  height: 6px;
  text-align: center;
  transition: all 0.4s;
}

.select__body {
  position: absolute;
  right: 0;
  left: 0;
  z-index: 5;
  display: none;
  width: 100%;
  max-height: 0;
  margin-top: 10px;
  padding: 0;
  overflow: hidden;
  border-top: none;
  border-radius: 0 0 2px 2px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}

.select__item {
  width: 100%;
  margin: 0 auto;
  padding: 0 8px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: capitalize;
  letter-spacing: 0.05em;
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.4s;
  user-select: none;
}
.select__item:hover {
  opacity: 1;
}

.select__wrapp {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 0 10px;
  background: var(--color-d);
  border: 1px solid var(--color-e);
  grid-gap: 8px;
}

.select__item:hover {
  color: #ffffff;
}

.main-title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 600;
  font-size: clamp(30px, 5vw, 60px);
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-a);
}

.show-more {
  width: 202px;
  height: 48px;
  border: 2px solid var(--plus-bg);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-a);
  transition: all 0.4s;
}
.show-more:hover {
  background-color: var(--plus-bg);
  transform: scale(1.05);
}
.show-more.disable {
  opacity: 0.3;
  pointer-events: none;
}

.start {
  position: relative;
}
.start__wrapper img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.start__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.start__default-dress {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.start__dress {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  display: none;
}
.start__dress--active {
  display: block;
}
.start__btn {
  position: fixed;
  top: 70vh;
  right: 25vw;
  z-index: 1;
}
.start__btn:hover.rotate {
  animation-name: rotate;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-iteration-count: 1s;
}
.start__btn:hover.pulse {
  animation-name: rotate;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(15deg);
    transform: scale(1);
    transform: scale(1);
  }
  66% {
    transform: rotate(-15deg);
    transform: scale(1.1);
    transform: scale(1.2);
  }
  100% {
    transform: rotate(0deg);
    transform: scale(1);
    transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.game {
  position: relative;
}
.game__preloader {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 2;
}
.game__preloader.done {
  opacity: 0;
  display: none;
}
.game__loader {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.3;
  background: #000;
}
.game__bg img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
}
.game__dress-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: none;
}
.game__dress-img.active {
  display: block;
}
.game__buttons-list {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 50px 60px;
  max-width: 540px;
  min-height: 820px;
  top: 190px;
  right: 265px;
  z-index: 5;
}
.game__buttons-item:nth-child(1) {
  display: none;
}
.game__buttons-item.active {
  display: block;
  border: 10px solid #18CF05;
}
.game__img {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
  width: 240px;
  height: 240px;
  border: 10px solid #605F5F;
  border-radius: 30px;
  transition: all 0.5s;
}
.game__img:hover {
  border: 10px solid #18CF05;
  z-index: 4;
}
.game__img.active {
  border: 10px solid #18CF05;
  z-index: 6;
}
.game__ok-btn {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 80px;
  right: 55vw;
  font-family: var(--font-family);
  font-size: 55px;
  font-weight: 400;
  color: #000;
  background-image: linear-gradient(46deg, #F8C339 30%, #FFE03D 70%, #ffffff 95%);
  box-shadow: 0px 40px 30px rgba(0, 0, 0, 0.25);
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: #FFE03D solid 3px;
  background-color: rgba(0, 0, 0, 0.4);
  display: none;
}
.game__ok-btn:nth-child(1) {
  background-image: linear-gradient(46deg, #F8C339 30%, #FFE03D 70%, #ffffff 95%);
  box-shadow: 0px 40px 30px rgba(0, 0, 0, 0.25);
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: #FFE03D solid 3px;
  background-color: rgba(0, 0, 0, 0.4);
}
.game__ok-btn.active {
  display: flex;
}
.game__ok-btn-none {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 80px;
  right: 55vw;
  font-family: var(--font-family);
  font-size: 55px;
  font-weight: 400;
  color: rgb(255, 255, 255);
  border: #fff solid 10px;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.4);
  display: none;
}
.game__ok-btn-none.active {
  display: flex;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.pulse-ok {
  animation-name: pulse;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-iteration-count: 6;
}

.scene-first {
  position: relative;
}
.scene-first__wrapper img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.scene-first__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.scene-first__default-dress {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 3;
}

@keyframes pulse-try {
  0% {
    transform: scale(1.2);
  }
  30% {
    transform: rotate(6deg);
  }
  40% {
    transform: rotate(-6deg);
  }
  50% {
    transform: rotate(6deg);
  }
  60% {
    transform: rotate(-6deg);
  }
  70% {
    transform: rotate(6deg);
  }
  80% {
    transform: rotate(-6deg);
  }
  100% {
    transform: rotate(0);
  }
}
.button-1 {
  opacity: 1;
  animation: button-1 0.5s;
}

.button-2 {
  opacity: 1;
  animation: button-2 1s;
}

.button-3 {
  opacity: 1;
  animation: button-2 1.5s;
}

.button-4 {
  opacity: 1;
  animation: button-2 2s;
}

.button-5 {
  opacity: 1;
  animation: button-2 2.5s;
}

@keyframes button-1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes button-2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes button-3 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes button-4 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes button-5 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.game__emoji {
  opacity: 0;
  position: absolute;
}
.game__emoji:nth-child(1) {
  display: none;
}
.game__emoji.active {
  display: block;
  opacity: 0;
  position: absolute;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  animation: game__emoji 2s;
  z-index: 50;
  transform: translateY(-100%);
}

@keyframes game__emoji {
  0% {
    opacity: 1;
    transform: translateY(100%);
  }
  90% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
.bubble {
  display: block;
  opacity: 1;
  position: absolute;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  animation: bubble 1s;
  z-index: 0;
  transform: scale(1);
}

@keyframes bubble {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.fail {
  display: none;
  opacity: 1;
  position: absolute;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  animation: fail 1s;
  z-index: 1;
  transform: scale(1);
}

@keyframes fail {
  0% {
    opacity: 0;
    transform: scale(4);
  }
  50% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.try-again-btn {
  position: fixed;
  top: 70vh;
  right: 25vw;
  z-index: 1;
  display: none;
  animation: try-btn 0.3s;
  transform: rotate(0);
  animation-iteration-count: 5;
}
.try-again-btn:hover {
  animation: pulse-try 1s;
}

@keyframes try-btn {
  0% {
    transform: rotate(0deg);
  }
  33% {
    transform: rotate(6deg);
  }
  66% {
    transform: rotate(-6deg);
  }
  100% {
    transform: rotate(0);
  }
}